@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}

#wrapper > div {
  padding-top: 100px;
}

#wrapper #main {
  padding-top: 0;
}

#wrapper #top {
  padding-top: 0;
}

#wrapper {
  background-image: url(/bullet-journal/static/media/worn_dots.bf799368.png);
  background-attachment: fixed;
  padding: 0;
}

*:focus {
  outline: none;
}
.home { 
    min-height: 800px;
}

.home__title {
    font-family: 'Permanent Marker', cursive;
    font-weight: 800;
    font-size: 9vw;
    text-align: center;
    padding-top: 50px;
}

.home__description {
    margin: 50px 100px;
    font-size: 1.4vw
}
.navbar {
    height: 5.5vw;
    background-color: #000547;
    z-index: 100;
    box-shadow:  0 5px 10px 0 rgba(0, 0, 0, 0.8), 0 20px 10px 0 rgba(0, 0, 0, 0.2);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    min-height: 50px;
}

.navbar__link {
    text-decoration: none;
    color: white;
    font-size: 1.4vw;
    padding: 1%;
}

.navbar__link:hover {
    background-color: #000222;
}
.list {
    min-height: 800px;
}

.list__wrapper {
    border: 10px dotted black;
    background-color: #c2d8d8;
    max-width: 600px;
    margin: 10px auto;
    border-radius: 25px;
    min-height: 500px;
    display: flex;
    flex-direction: column;
}

.list__button {
    background-color: #000547;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
    margin-top: auto;
    cursor: pointer;
}

.list__button:hover {
    color: greenyellow;
}

.todos__wrapper {
    display: flex;
    flex-direction: column;
    margin: 20px auto;
    max-width: 400px;
    font-family: "Comic Sans MS", cursive, sans-serif;
    list-style: none;
}

ul, li {
    margin: 0;
    padding: 0;
}

.todos__checkbox {
    margin: 10px;
    height: 20px;
    width: 20px;
}

.todos__item {
    margin: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-size: 1.4vw;
    align-items: center;
}

.addTodo input {
    border: none;
    background-color: seashell;
    padding: 10px;
    margin: 15px 10px 15px 15px;
    width: 80%;
    height: 30px;
    border-radius: 10px;
}

.tracker {
    min-height: 800px;
}

.tracker__wrapper {
    margin: auto;
    max-width: 850px;
    background-color: #c2d8d8;
    padding: 10px;
}

table, th, td {
    border: 1px solid black;
    border-collapse: collapse;
    background-color: white;
}

td {
    height: 20px;
    width: 20px;
}

th {
    min-width: 100px;
}

.row {
    display: flex;
    
}
